"use client";

import { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/Card";
import { ChevronLeft, ChevronRight } from "lucide-react";
import { useToast } from "@/hooks/use-toast";
import { motion } from "framer-motion";
import { mockHalls } from "@/constants/dataHall";
export interface Hall {
    id: string;
    name: string;
    location: string;
    capacity: number;
    status?: "available" | "reserved";
}


export default function AdminHallsPage() {
    const { toast } = useToast();

    const [halls, setHalls] = useState<Hall[]>([]);
    const [loading, setLoading] = useState(false);
    const [totalItems, setTotalItems] = useState(0);
    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 6;

    const fetchHalls = async () => {
        setLoading(true); try {
            const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/halls?page=${currentPage}&limit=${itemsPerPage}`,
                {
                    credentials: "include",
                });

            const data = await res.json();

            if (!res.ok) {
                toast({
                    title: "Failed to fetch halls",
                    description: data.message || "Something went wrong.",
                    variant: "destructive",
                });
                return;
            }

            setHalls(data.halls || []);
            setTotalItems(data.totalItems || 0);
        } catch (error: any) {
            toast({
                title: "Error",
                description: error.message || "Unexpected error occurred.",
                variant: "destructive",
            });
        } finally {
            setLoading(false);
        }

    };

    useEffect(() => { fetchHalls(); }, [currentPage]);

    return (
        <div className="p-6 space-y-6">
            <div className="flex justify-between items-center">
                <h2 className="text-2xl font-bold">All Halls</h2>
                {/* يمكن إضافة زر إضافة قاعة جديدة هنا */}
            </div>

            {loading ? (
                <div className="flex justify-center items-center h-[50vh]">
                    <div className="animate-spin h-10 w-10 border-4 border-purple-500 border-t-transparent rounded-full" />
                </div>
            ) : mockHalls.length > 0 ? (
                <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 xl:grid-cols-4 gap-6">
                    {mockHalls.map((hall) => (
                        <motion.div
                            key={hall.id}
                            initial={{ opacity: 0, scale: 0.95 }}
                            animate={{ opacity: 1, scale: 1 }}
                            transition={{ duration: 0.3 }}
                        >
                            <Card className="border-2 border-gray-200">
                                <CardContent className="p-4">
                                    <h3 className="text-lg font-bold mb-1">{hall.name}</h3>
                                    <p className="text-sm text-gray-600">{hall.location}</p>
                                    <p className="text-sm text-gray-500">Capacity: {hall.capacity}</p>
                                    {hall.status && (
                                        <p className="text-sm mt-1 text-gray-500 capitalize">
                                            Status: {hall.status}
                                        </p>
                                    )}
                                </CardContent>
                            </Card>
                        </motion.div>
                    ))}
                </div>
            ) : (
                <p className="text-gray-500 text-center">No halls found.</p>
            )}

            {totalItems > itemsPerPage && (
                <div className="flex justify-center mt-6">
                    <nav className="flex items-center gap-2">
                        <Button
                            variant="ghost"
                            size="icon"
                            disabled={currentPage === 1}
                            onClick={() => setCurrentPage((prev) => Math.max(1, prev - 1))}
                        >
                            <ChevronLeft />
                        </Button>
                        {Array.from({
                            length: Math.ceil(totalItems / itemsPerPage),
                        }).map((_, i) => (
                            <Button
                                key={i}
                                variant={currentPage === i + 1 ? "default" : "outline"}
                                onClick={() => setCurrentPage(i + 1)}
                            >
                                {i + 1}
                            </Button>
                        ))}
                        <Button
                            variant="ghost"
                            size="icon"
                            disabled={currentPage === Math.ceil(totalItems / itemsPerPage)}
                            onClick={() => setCurrentPage((prev) => prev + 1)}
                        >
                            <ChevronRight />
                        </Button>
                    </nav>
                </div>
            )}
        </div>

    );
}